<template>
	<div class="wrapper">
		<nav-bar></nav-bar>
		<div class="pages__wrapper">
			<div class="collor__pallete">
				<green-button @click="changeColor">
					Текущее значение {{ currentIndex + 1 }}
				</green-button>
				<green-button @click="color1"> 1 </green-button>
				<green-button @click="color2"> 2 </green-button>
				<green-button @click="color3"> 3 </green-button>
				<green-button @click="color4"> 4 </green-button>
				<green-button @click="color5"> 5 </green-button>
				<green-button @click="color6"> 6 </green-button>
			</div>
			<router-view></router-view>
		</div>
		<div class="footer"></div>
	</div>
</template>

<script>
import NavBar from '@/components/UI/NavBar.vue';
export default {
	components: { NavBar },
	data() {
		return {
			values: [
				'#4C5633',
				'#2E714D',
				'#4FC973',
				'#43B17B',
				'#6D8C67',
				'#609358',
			],
			currentIndex: 0,
		};
	},
	computed: {
		currentValue() {
			return this.values[this.currentIndex];
		},
	},
	methods: {
		changeColor() {
			this.currentIndex = (this.currentIndex + 1) % this.values.length;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color1() {
			this.currentIndex = 0;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color2() {
			this.currentIndex = 1;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color3() {
			this.currentIndex = 2;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color4() {
			this.currentIndex = 3;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color5() {
			this.currentIndex = 4;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
		color6() {
			this.currentIndex = 5;
			document.documentElement.style.setProperty(
				'--complementary-color',
				this.currentValue
			);
		},
	},
};
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');
:root {
	--bg-color: #202124;
	--complementary-color: #4c5633;
	--complementary-dub-color: blue;
	--text-color: white;
	--shadow-color: rgba(0, 0, 0, 0.2);
	--border-color: white;
	--bg-color-shadow: #2021243f;

	--normal-text: 1rem;
	--mini-text: 0.8rem;

	--text-shadow-green-blue: 1px 1px 2px var(--complementary-color),
		0 0 1em var(--complementary-color),
		0 0 0.2em var(--complementary-dub-color);
}
