<template>
	<div>
		<product-cards :cards="sortedAndSearchedCards"> </product-cards>
	</div>
</template>

<script>
export default {
	name: 'fetch-filter-cards-first',
	data() {
		return {
			cards: [],
			fetchURL: 'https://jsonplaceholder.typicode.com/photos',
			limitMin: 0,
			limitMax: 100,
			// limit: 10,
			// totalPages: 0,
			// searchQuery: '',
			// selectedSort: '',
		};
	},
	methods: {
		async fetchCard() {
			try {
				setTimeout(async () => {
					let response = await fetch(this.fetchURL)
						.then((response) => response.json())
						.then(
							(json) =>
								(this.cards = json.slice(
									this.limitMin,
									this.limitMax
								))
						);
					console.log(this.cards);
					console.log(response);
				}, 1000);
			} catch (e) {
				alert('Ошибка!');
			} finally {
			}
		},
	},
	mounted() {
		this.fetchCard();
	},
	computed: {
		sortedAndSearchedCards() {
			return this.cards;
		},
	},
};
</script>

